import * as React from 'react'
import { StatelessComponent } from 'react'
import styled from 'styled-components'
import SectionTemplate from './templates/SectionTemplate'
import { ON_MOBILE } from './Responsive'

const Wrapper = styled.div`
  max-width: 900px;
  margin: auto;
  padding: 0 10px;
  font-size: 14px;
  overflow: auto;
  /* word-break: keep-all; */

  @media ${ON_MOBILE} {
    font-size: 10px;
  }
`

const Table = styled.table`
  width: 100%;
  min-width: 550px;
  border-collapse: collapse;
  border: 1px solid #4a4a4a;
  table-layout: fixed;
`

const Row = styled.tr`
  border: 1px solid #4a4a4a;
`

const Header = styled.th`
  border: 1px solid #4a4a4a;
  padding: 8px;
`

const Data = styled.td`
  border: 1px solid #4a4a4a;
  text-align: center;
  padding: 8px;
`

const Notice = styled.div`
  margin-top: 15px;
  padding: 20px;
  font-size: 15px;
  font-weight: bold;
  text-align: center;
`

const SimpleScheduleSection: StatelessComponent<{}> = () => (
  <SectionTemplate title="" iconClass="fa fa-clock-o fa-2x">
    <Wrapper>
      <Table>
        <thead>
          <Row>
            <Header rowSpan={2}></Header>
            <Header>Track </Header>
            <Header>Track </Header>
            <Header>Track </Header>
            <Header>Track IV</Header>
            <Header>Track V</Header>
          </Row>
          <Row>
            <Header>11 A</Header>
            <Header>11 B</Header>
            <Header>11 C</Header>
            <Header>11 D</Header>
            <Header>11 E</Header>
          </Row>
        </thead>
        <tbody>
          <Row>
            <Header>10:00 ~ 10:30</Header>
            <Data colSpan={5}>Registration</Data>
          </Row>
          <Row>
            <Header>10:30 ~ 11:10</Header>
            <Data rowSpan={2} colSpan={3}>
               <br /> 
            </Data>
            <Data>IoT Labs</Data>
            <Data rowSpan={2} />
          </Row>
          <Row>
            <Header>11:10 ~ 11:50</Header>
            <Data>ElasticSearch</Data>
          </Row>
          <Row>
            <Header>11:50 ~ 13:00</Header>
            <Data colSpan={5}>Lunch</Data>
          </Row>
          <Row>
            <Header>13:00 ~ 13:40</Header>
            <Data>Keras Korea</Data>
            <Data></Data>
            <Data>  </Data>
            <Data rowSpan={4}>Microsoft</Data>
            <Data rowSpan={4}>Korea Azure User Group (KRAZURE)</Data>
          </Row>
          <Row>
            <Header>13:40 ~ 14:20</Header>
            <Data>Taeyo.NET</Data>
            <Data>JetBranis  </Data>
            <Data> </Data>
          </Row>
          <Row>
            <Header>14:20 ~ 14:40</Header>
            <Data colSpan={3}>Break</Data>
          </Row>
          <Row>
            <Header>14:40 ~ 15:20</Header>
            <Data></Data>
            <Data>EFL  </Data>
            <Data>C++ Korea</Data>
          </Row>
          <Row>
            <Header>15:20 ~ 16:00</Header>
            <Data>MongoDB Korea</Data>
            <Data>Nogata Jun (Japan)</Data>
            <Data>ASP.NET Korea User Group</Data>
            <Data rowSpan={4}>Jboss user Group</Data>
            <Data rowSpan={4}></Data>
          </Row>
          <Row>
            <Header>16:00 ~ 16:30</Header>
            <Data colSpan={3}>Break</Data>
          </Row>
          <Row>
            <Header>16:30 ~ 17:10</Header>
            <Data> </Data>
            <Data> (HarmonyCar)</Data>
            <Data></Data>
          </Row>
          <Row>
            <Header>17:10 ~ 17:50</Header>
            <Data> </Data>
            <Data>  </Data>
            <Data>TNF/Needlworks</Data>
          </Row>
          <Row>
            <Header>17:50 ~ 18:00</Header>
            <Data colSpan={5}>  </Data>
          </Row>
        </tbody>
      </Table>
    </Wrapper>
    <Notice>
       Workshop      Worksop 
      spicalong@gmail.com   .
    </Notice>
  </SectionTemplate>
)

export default SimpleScheduleSection



